import BaseService from "../../common/services/BaseService";
import {instance as axiosClient} from "../../common/helpers/axios";
class AuthService extends BaseService{
   hasToken (token) {
     if(!token){
       token =  JSON.parse(localStorage.getItem("authenticated"));
     }
    if (token) {
      return true
    }
    return false;
  }

  setToken (token) {
    localStorage.setItem("authenticated", JSON.stringify(token));
  }


  signUserIn=(state)=>{
    const requestOptions = {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        }
      }
      // const passwordTobeKept = ""+state.first_name+"1234";
      // state.password = passwordTobeKept;
      // delete state.errMsg;
      console.log(state);
      
      return axiosClient.post('users/signin', state,requestOptions)
  } 
}
export default AuthService;
