import axios from  'axios';

// -- Redux
import {  useDispatch} from "react-redux"

// -- User actions 
import { logoutUser } from '../../auth/redux/actions/auth';

export const instance = axios.create({
        baseURL : 'https://tilosia-arma-web-backend.herokuapp.com/api/v1',//staging
        // baseURL : 'http://127.0.0.1:8848/api/v1', //local
});

 // Set the AUTH token for any request
 instance.interceptors.request.use(function (config) {
        const token = JSON.parse(localStorage.getItem('authenticated'));
        config.headers.Authorization =  token ? `Bearer ${token}` : '';
        return config;
});

//log user out if token is expired
instance.interceptors.response.use(response => {
        return response;
     }, error => {
       if (error.response.status === 401) {
        // useDispatch(logoutUser());
       }
       return error;
     });